<template>
  <el-row>
    <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
      <h1 class="pageTitle">{{ this.pageTitle }}</h1>
    </el-col>
    <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
      <el-form label-position="left" label-width="250px" v-loading="loading" :model="form">
        <el-row :gutter="20">
          <el-col
            :xs="24"
            :sm="24"
            :md="12"
            :lg="12"
            :xl="12"
            v-for="(field) in this.fields"
            :key="field.field_id"
          >
            <el-form-item
              :label="requiredLabelField(field.field_label, field.is_required)"
              v-if="hiddenField(field.is_hidden)"
            >
              <el-input v-if="textField(field.type_field)" v-model="form[field.field_table_name]"></el-input>
              <el-switch
                v-if="switchField(field.type_field)"
                v-model="form[field.field_table_name]"
              ></el-switch>
              <el-date-picker
                v-if="dateField(field.type_field)"
                v-model="form[field.field_table_name]"
                type="date"
                format="dd/MM/yyyy"
              ></el-date-picker>

              <el-select
                v-model="form[field.field_table_name]"
                width="100%"
                v-if="selectField(field.type_field)"
                clearable
                placeholder="Selecione"
              >
                <el-option
                  v-for="item in fieldsOptions[field.field_table_name]"
                  :key="item.dropdown_option_value"
                  :label="item.dropdown_option_value"
                  :value="item.dropdown_option_value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>

          <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <el-form-item>
              <el-button type="primary" @click="submit()">Criar Oportunidade</el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: "DealCreate",
  data() {
    return {
      loading: false,
      pageTitle: "Nova Oportunidade",
      fields: [],
      form: {},
      fieldsOptions: {},
    };
  },
  methods: {
    textField(typeField) {
      if (typeField === "text") {
        return true;
      } else {
        return false;
      }
    },
    switchField(typeField) {
      if (typeField === "switch") {
        return true;
      } else {
        return false;
      }
    },
    dateField(typeField) {
      if (typeField === "date") {
        return true;
      } else {
        return false;
      }
    },
    selectField(typeField) {
      if (typeField === "dropdown") {
        return true;
      } else {
        return false;
      }
    },
    hiddenField(param) {
      if (param == 0) {
        return true;
      } else {
        return false;
      }
    },
    requiredLabelField(label, is_required) {
      if (is_required == "1") {
        return label + " *";
      } else {
        return label;
      }
    },
    submit() {
      this.loading = true;
      this.$http
        .post("/module/deals", this.form)
        .then((res) => {
          this.loading = false;
          this.$notify({
            title: "Sucesso!",
            message: res.data.msg,
            type: "success",
            duration: 3000,
          });

          this.$router.push({
            name: "Deals_List",
          });
        })
        .catch((err) => {
          this.loading = false;
          this.$notify({
            title: "Erro",
            message: err.response.data,
            type: "error",
          });
        });
    },
    getFields() {
      this.$http.get("/fields/deals").then((res) => {
        this.fields = res.data;
        this.fields.forEach((element) => {
          if (element.type_field === "dropdown") {
            this.$http
              .get("/fields/dropdown-values/" + element.field_id)
              .then((df) => {
                this.$set(
                  this.fieldsOptions,
                  element.field_table_name,
                  df.data
                );
              });
          }

          this.$set(this.form, element.field_table_name, "");
        });
      });
    },
  },
  mounted() {
    this.getFields();
  },
  head: {
    title: {
      inner: "Nova Oportunidade",
      complement: "GuiaSales",
    },
  },
};
</script>

<style>
.el-form-item__label {
  color: #666;
}
</style>